import Vue from 'vue';
import Vuetify from 'vuetify/lib';

// Languages
import es from '../locale/es';
import en from '../locale/en';

Vue.use(Vuetify);

const THEME_COLORS = {
    primary: '#e97c21',
    secondary: '#378ccc',
    roma_rcolor: '#81007e',
    roma_acolor: '#f9d912',
    accent: '#82B1FF',
    error: '#FF5252',
    info: '#2196F3',
    success: '#4CAF50',
    warning: '#FFC107',
};

export default new Vuetify({
  theme: {
      options: {
        customProperties: true,
      },
    themes: {
      light: THEME_COLORS,
    },
  },
    lang: {
      locales: { es, en },
      // Verificamos namespace roma y tomamos su lenguaje
      current: (sessionStorage.roma) ? JSON.parse(sessionStorage.roma).selectedLanguage : 'es',
    },
  icons: {
    iconfont: 'fa',
  },
});
