import Vue from 'vue';
import Router from 'vue-router';
import HomePage from './views/Home.vue';

// Partials
import RomaHeader from '@/components/partial/RomaHeader.vue';
import RomaFooter from '@/components/partial/RomaFooter.vue';

Vue.use(Router);

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'home',
            components: {
                header: RomaHeader,
                default: HomePage,
                footer: RomaFooter,
            },
            children: [
                {
                    path: '/organizer', // Sin el slash obliga a pasar por el path de su padre
                    name: 'organizer',
                    // route level code-splitting
                    // this generates a separate chunk (organizer.[hash].js) for this route
                    // which is lazy-loaded when the route is visited.
                    component: () => import('./views/Organizer.vue'),
                    children: [
                        {
                            path: 'unit/:id',
                            name: 'organizer-unit',
                            component: () => import('./views/organizer/Unit.vue'),
                        },
                    ],
                },
                {
                    path: '/tables',
                    name: 'tables',
                },
                {
                    path: '/evaluations',
                    name: 'evaluations',
                },
                {
                    path: '/results',
                    name: '/results',
                },
                {
                    path: '/polls',
                    name: 'polls',
                },
                {
                    path: '/administration',
                    name: 'administration',
                },
            ],
        },
    ],
});
