

































































import Vue from 'vue';
import { mapState } from 'vuex';

export default Vue.extend({
    name: 'RomaFooter',
    props: {},
    data() {
        return {
            language: this.$store.state.config.selectedLanguage,
        };
    },
    computed: {
        ...mapState({
            contacts: (state) => {
                return [
                    {icon: 'phone-volume', text: `Teléfono SAC: ${state.info.contact.sac}`},
                    {icon: 'mobile', text: `Teléfono Mobil: ${state.info.contact.phone}`},
                    {icon: 'at', text: `Correo Electrónico: ${state.info.contact.email}`, url: `mailto://${state.info.contact.email}`},
                ];
            },
            social: (state) => state.info.social,
            languages: (state) => state.languages,
        }),
    },
    methods: {
      selectLanguage() {
          this.$store.commit('selectLanguage', this.language);
      },
    },
});
