import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
// Plugins Vuex
import VuejsStorage from 'vuejs-storage';
// Default language by vuetify
import Vuetify from '../src/plugins/vuetify';
// api example
import exampleJson from './ApiExample';

Vue.use(Vuex);

// Configuración básicas axios
const AXIOS_DEFAULT_CONFIG = {
    baseURL: 'https://reqres.in/api',
    timeout: 15000, // Si nunca responde el servidor
    responseType: 'json',
};

export default new Vuex.Store({
  state: {
    user: {},
    config: {
      selectedLanguage: Vuetify.preset.lang.current,
    },
    languages: [
      // Iconos tomados de: https://www.flaticon.com/packs/countrys-flags
      {title: 'Español', i18n: 'es', icon: 'https://image.flaticon.com/icons/png/512/197/197593.png'},
      {title: 'Ingles', i18n: 'en', icon: 'https://image.flaticon.com/icons/png/128/197/197484.png'},
    ],
    snackbar: {
      show: false,
      timeout: 5000,
      color: 'info',
      text: ''

    },
    masters: {
      preLearning: [],
      skills: [],
      attitudes: [],
    },
    roma: [
      {
        id: 1,
        name: 'Básica',
        subjects: [
          {name: 'Lenguaje', levels: [1, 2, 3, 4, 5]},
          {name: 'Matemáticas', levels: [1, 2, 3, 4, 5, 6, 7, 8]},
          {name: 'Ciencias Naturales', levels: [1, 2, 3]},
          {name: 'Historia, Geografía y Ciencias Sociales', levels: [1, 2]},
        ],
      },
      {
        id: 2,
        name: 'Media',
        subjects: [
          {name: 'Lenguaje', levels: [1, 2, 3]},
          {name: 'Matemáticas', levels: [1, 2, 3, 4, 5, 6]},
          {name: 'Historia Geográfica / Ciencias Sociales', levels: [1, 2]},
          {name: 'Ciencias Naturales', levels: [1, 2, 3]},
        ],
      },
    ],
    schools: exampleJson.data,
    organizer: {
      selectedSchool: exampleJson.data[0],
      selectedPeriod: new Date().getFullYear(),
    },
    info: {
      contact: {
        phone: '+569 3458 0660',
        sac: '229293270',
        email: 'sac@romacl.com',
        location: {lat: '10.2099343', lon: '-68.0119684,12z'},
      },
      social: [
        {icon: 'instagram', url: 'http://instagram.com/'},
        {icon: 'facebook', url: 'http://facebook.com/'},
        {icon: 'youtube', url: 'http://youtube.com/'},
        // {icon: 'twitter', url: 'http://twitter.com/'},
      ],
    },
  },
  getters: {
    // School Filter
    getSchoolBy(state) {
      return (meta: string, value: any) => {
        return state.schools.find(
            (school: object) => {
              return school[meta] === value;
            },
        );
      };
    },
    // Unit Filter
    getUnitBy(state) {
      return (meta: string, value: string) => {
        return state.organizer.selectedSchool.units.find(
            (unit: object) => {
              return unit[meta] === value;
            },
        );
      };
    },
  },
  mutations: {
    // Configs
    login(state, user: object) {
      state.user = user;
    },
    selectLanguage(state, language: object) {
      Vuetify.framework.lang.current = language; // Cambia lenguaje de vuetify
      state.config.selectedLanguage = language;
    },
    // School
    selectSchool(state, school: object) {
      state.organizer.selectedSchool = school;
    },
    selectUnit(state, unit) {
      state.organizer.selectedSchool.selectedUnit = unit;
    },
    //Masters
    setMaster(state, master: object) {
      if (master.id && master.item) {
        const id = master.id;
        let item = master.item;
        if (!item.id) {
          item.id = Math.floor(Math.random() * (9999 - 1)) + 1; // Para futura referencia de modificar
          state.masters[id].push(item);
        } else {
          // Simula la modificación de unidad
          const index = state.masters[id].findIndex((item) => item.id === state.masters[id].id);
          Vue.set(state.masters[id], index, item);
        }

        return true;
      }

      return false;
    },
    delMaster(state, master: object) {
      if (master.item && master.id) {

        if(Array.isArray(master.item)) {
          master.item.forEach(i => {
            // TODO: terminar eliminacion multiple
            const index = state.masters[master.id].indexOf(master.item);
            state.masters[master.id].splice(index, 1);
          })
        } else {
          const index = state.masters[master.id].indexOf(master.item);
          state.masters[master.id].splice(index, 1);
        }

        return true;
      }

      return false;
    },
    // Unit
    setUnit(state, data: object) {
      if (data.school.units && data.unit) {
        if (!data.unit.id) {
          data.unit.id = Math.floor(Math.random() * (9999 - 1)) + 1; // Para futura referencia de modificar
          data.school.units.push(data.unit);
        } else {
          // Simula la modificación de unidad
          const index = data.school.units.findIndex((unit) => unit.id === data.unit.id);
          Vue.set(data.school.units, index, data.unit);
        }

        return true;
      }

      return false;
    },
    delUnit(state, data: object) {
      if (data.school.units && data.unit) {
        const index = data.school.units.indexOf(data.unit);
        data.school.units.splice(index, 1);
      }
    },
  },
  actions: {
    // User
    async login({ commit }, request) {
        try {
            const ran = Math.floor(Math.random() * (10 - 1)) + 1;
            const user = await axios({
              ...AXIOS_DEFAULT_CONFIG,
              // propiedades no comunes
              url: '/users/' + ran,
              method: 'get',
              data: request,
            });

            commit('login', user.data.data);
        } catch (errors) {
          console.log(errors);
        }
    },
    // Unit
  },
  plugins: [
    VuejsStorage({
      keys: [
          // Webapp Configs
          'config',
          'languages',
          // Data offline
          'masters',
          'schools',
          // User Selecteds
          'organizer',
      ],
      namespace: 'roma',
      driver: VuejsStorage.drivers.localStorage,
    }),
  ],
});
